export default {
  data() {
    return {
      USER_ID_PREFIX: this.global.USER_ID_PREFIX,
      AUTHORIZATION: this.global.AUTHORIZATION,
      RERRESH_TOKEN: this.global.RERRESH_TOKEN,
      USER_INFO: this.global.USER_INFO,
      USER_PHONE: this.global.USER_PHONE,
      type: "1",
      username: "",
      password: "",
      authCode: "",
      disabled: false,
      countdown: 60,
      codeLabel: "发送"
    }
  },
  methods: {
    changeType(type) {
      this.type = type;
    },
    formSubmit() {
      if (this.isBlank(this.username) || this.isBlank(this.password) || this.isBlank(this.authCode)) {
        this.$msg('账号、密码和验证码不能为空');
        return;
      }
      let that = this;
      let param = new Object();
      param.username = this.username;
      param.password = this.password;
      param.type = this.type;
      param.authCode = this.authCode;
      this.$minApi.uniapp_post(this.$base_api.login, JSON.stringify(param), 'json').then(login_res => {
        if (login_res.success) {
          that.$msg("登陆成功");
          let data = login_res.data;
          // let token = data.token;
          that.$cache.set(that.USER_ID_PREFIX, data.uid);
          //小程序登陆有效期设置为7天
          // that.$cache.set(that.AUTHORIZATION, token, 86400);
          that.$cache.set(that.USER_INFO, data, 86400);
          //跳转到我的页面
          that.$openPage('petrolList');
        } else {
          that.$msg("登录失败" + login_res.msg);
        }
      });
    },
    sendCode() {
      let that = this;
      if (this.isBlank(this.username)) {
        this.$msg('账号不能为空');
        return;
      }
      let param = new Object();
      param.username = this.username;
      param.type = this.type;
      this.disabled = true;
      this.$minApi.uniapp_post(this.$base_api.getAuthCode, JSON.stringify(param), 'json').then(res => {
        if (res.success) {
          this.$msg("发送成功");
          this.countdown = 60;
          this.settime();
        } else {
          that.$msg("发送失败" + res.msg);
          this.disabled = false;
        }
      });
    },
    settime() {
      let that = this;
      this.codeLabel = this.countdown + " s";
      if (this.countdown == 0) {
        this.countdown = 60;
        this.disabled = false;
        this.codeLabel = "重新发送";
        return;
      } else {
        this.countdown--;
      }
      setTimeout(function() {
        that.settime();
      }, 1000)
    }
  },
  onShow() {

    if (wx.hideHomeButton) {
      wx.hideHomeButton();
    }

  }

}
